.promo {
  padding: 136px 103px 120px;

  @include vp-1199 {
    padding: 95px 45px 100px;
  }

  @include vp-767 {
    padding: 70px 15px 70px;
  }

  h2 {
    padding: 0;
    margin: 0;
    margin-bottom: 34px;
    font-family: $ff-primary;
    font-weight: 600;
    font-size: 54px;
    line-height: 64.8px;
    letter-spacing: 1.2px;
    text-align: center;

    @include vp-1199 {
      margin-bottom: 43px;
      font-size: 42px;
      line-height: 43.68px;
      letter-spacing: 1.2px;
    }

    @include vp-767 {
      margin-bottom: 42px;
      font-size: 32px;
      line-height: 38.4px;
      letter-spacing: 1.2px;
    }
  }
}

.promo__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(2, 1fr);
  gap: 40px;

  @include vp-1199 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(3, 1fr);
    gap: 30px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
  }
}

.promo__container {
  background-color: $color-torch-red;
  grid-column: 1/2;
  grid-row: 1/3;

  @include vp-1199 {
    grid-column: 1/3;
    grid-row: 1/3;
  }

  h4 {
    font-family: $ff-secondary;
    font-weight: 700;
    font-size: 28px;
    line-height: 43.68px;
  }

  span {
    display: block;
    font-family: $ff-secondary;
    font-weight: 700;
    font-size: 70px;
    line-height: 60px;
  }

  p {
    font-family: $ff-secondary;
    font-weight: 400;
    font-size: 18px;
    line-height: 25.2px;
  }
}

.promo__container-description {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-between;

  @include vp-767 {
    display: flex;
    flex-direction: column;
  }
}

.promo__description {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 36px 45px 44px;

  @include vp-767 {
    text-align: center;
  }

  h4 {
    padding: 0;
    margin: 0;
  }

  span {
    padding: 0;
    margin: 0;
  }

  p {
    padding: 0;
    margin: 0;
  }

  &--secondary {
    height: 100%;
    padding: 15px 35px 25px 25px;

    @include vp-1199 {
      padding: 15px 25px 30px 27px;
    }

    @include vp-767 {
      padding: 25px 25px 35px 27px;
    }

    p {
      @include vp-767 {
        margin-bottom: 25px;
      }
    }
  }
}

.promo__description-primary {
  color: $color-default-white;

  span {
    margin-bottom: 20px;
  }

  p {
    @include vp-767 {
      margin-bottom: 20px;
    }
  }
}

.promo__description-secondary {
  color: $color-resolution-blue;

  h4 {
    display: inline-block;
  }

  p {
    display: block;
  }
}

.promo__link {
  padding: 0;
  margin: 0;

  a {
    display: inline-block;
    padding: 0;
    margin: 0;
    font-family: $ff-secondary;
    font-weight: 700;
    font-size: 14px;
    line-height: 16.41px;
    letter-spacing: 1.5px;
    text-decoration: 1px underline;
    text-underline-offset: 5px;
    text-transform: uppercase;
    color: $color-default-white;

    &:hover {
      text-decoration: 1px underline;
      text-underline-offset: 5px;
      color: $color-default-white;
      opacity: 0.8;
    }

    &:focus-visible {
      outline: 1px solid $color-default-white;
      outline-offset: 5px;
      text-decoration: none;
    }

    &:active {
      outline: none;
      text-decoration: 1px underline;
      text-underline-offset: 5px;
      color: $color-default-white;
      opacity: 1;
    }
  }
}

.promo__photo {
  width: 100%;
  height: 100%;
  max-width: 280px;
  justify-content: right;

  @include vp-1199 {
    width: 100%;
    height: 100%;
    min-width: 399px;
    min-height: 462px;
  }

  @include vp-767 {
    min-width: 290px;
    min-height: 299px;
  }

  img {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    object-fit: cover;
  }
}

.promo__box {
  background-color: $color-default-white;
  border: 7px solid $color-smalt;
  border-image: $torch-crimson-to-smalt;
  border-image-slice: 1;
}

.promo__box-description {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  h4 {
    font-family: $ff-secondary;
    font-weight: 700;
    font-size: 26px;
    line-height: 43.68px;
  }

  p {
    font-family: $ff-secondary;
    font-weight: 400;
    font-size: 16px;
    line-height: 22.4px;
  }

  a {
    display: inline-block;
    padding: 0;
    margin: 0;
    font-family: $ff-secondary;
    font-weight: 700;
    font-size: 12px;
    line-height: 14.06px;
    text-decoration: 1px underline;
    text-underline-offset: 5px;
    color: $color-default-sapphire;

    &:hover {
      text-decoration: 1px underline;
      text-underline-offset: 5px;
      color: $color-default-sapphire;
      opacity: 0.8;
    }

    &:focus-visible {
      outline: 1px solid $color-default-sapphire;
      outline-offset: 5px;
      text-decoration: none;
    }

    &:active {
      outline: none;
      text-decoration: 1px underline;
      text-underline-offset: 5px;
      color: $color-default-sapphire;
      opacity: 1;
    }
  }
}
